import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import { TextField } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props as ApplicationProps } from '../index';
import { MaritalStatus } from './index';

const ADDRESS = STRINGS.APPLICATION.PERSONAL_LOAN.ADDRESS;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 96
  }
});
interface Props extends ApplicationProps {
  maritalStatuses: MaritalStatus[]
}

function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      borrower: {
        [key]: value
      },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextField
            label={ADDRESS.STREET}
            onChange={(value: string) => onApplicationChange('street', value)}
            value={borrower.street}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextField
            label={ADDRESS.PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('residentialNumber', value)}
            value={borrower.residentialNumber}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Form;