import React from 'react';
import Grid from '@material-ui/core/Grid';

import STRINGS from '../../../../strings';
import { BankRadioButtons } from '../../../../components/RadioButtons';
import { BacLogoImg, MultibankLogoImg, StGeorgeLogoImg, GlobalBankLogoImg } from '../../../../components/Images';

import { Props } from './index';

const options = [
  {
    logo: MultibankLogoImg,
    value: "multibank",
    amount: 5000.00,
    payments: 18,
    monthlyFee: 280.55
  },
  {
    logo: StGeorgeLogoImg,
    value: "st_george",
    amount: 5000.00,
    payments: 18,
    monthlyFee: 280.75,
  },
  {
    logo: GlobalBankLogoImg,
    value: "global_bank",
    amount: 5000.00,
    payments: 18,
    monthlyFee: 281.65,
  },
  {
    logo: BacLogoImg,
    value: "bac",
    amount: 5000.00,
    payments: 18,
    monthlyFee: 287.54
  },
];

function Banks(props: Props) {
  const { quotation, setQuotation } = props;

  function onOptionPressed(bank: string) {
    setQuotation({ loan: { bank } });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <BankRadioButtons
          label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_BANK}
          options={options}
          onClick={onOptionPressed}
          value={quotation.loan.bank}
        />
      </Grid>
    </Grid>
  );
};

export default Banks;