import React, { useState } from 'react';
import {
  STEPS,
  Application
} from "../types";
import Forms from '../Forms';

interface Stepper {
  next?: () => void;
  prev?: () => void;
  disabled: boolean;
  Form: React.ReactElement;
  step: STEPS
};

function useStep(
  application: Application, 
  setApplication: (application: Partial<Application>) => void
): Stepper {
  const [step, setStep] = useState(STEPS.DOCUMENTS);

  const props = {
    application, 
    setApplication
  };

  return ({
    [STEPS.DOCUMENTS]: () => ({
      step,
      next: () => setStep(STEPS.BORROWER),
      disabled: false,
      Form: <Forms.Documents {...props} />
    }),
    [STEPS.BORROWER]: () => ({
      step,
      next: () => setStep(STEPS.ADDRESS),
      prev: () => setStep(STEPS.DOCUMENTS),
      disabled: false,
      Form: <Forms.Borrower {...props} />
    }),
    [STEPS.ADDRESS]: () => ({
      step,
      next: () => setStep(STEPS.WORK),
      prev: () => setStep(STEPS.BORROWER),
      disabled: false,
      Form: <Forms.Address {...props} />
    }),
    [STEPS.WORK]: () => ({
      step,
      next: () => setStep(STEPS.PERSONAL_REFERENCE_RELATED),
      prev: () => setStep(STEPS.ADDRESS),
      disabled: false,
      Form: <Forms.Work {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_RELATED]: () => ({
      step,
      next: () => setStep(STEPS.PERSONAL_REFERENCE_UNRELATED),
      prev: () => setStep(STEPS.WORK),
      disabled: false,
      Form: <Forms.PersonalReferenceRelated {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_UNRELATED]: () => ({
      step,
      next: () => setStep(STEPS.HEALTH_1),
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_RELATED),
      disabled: false,
      Form: <Forms.PersonalReferenceUnrelated {...props} />
    }),
    [STEPS.HEALTH_1]: () => ({
      step,
      next: () => setStep(STEPS.HEALTH_2),
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_UNRELATED),
      disabled: false,
      Form: <Forms.Health1 {...props} />
    }),
    [STEPS.HEALTH_2]: () => ({
      step,
      next: () => setStep(STEPS.HEALTH_3),
      prev: () => setStep(STEPS.HEALTH_1),
      disabled: false,
      Form: <Forms.Health2 {...props} />
    }),
    [STEPS.HEALTH_3]: () => ({
      step,
      prev: () => setStep(STEPS.HEALTH_2),
      disabled: false,
      Form: <Forms.Health3 {...props} />
    })
  })[step]();
};

export default useStep;