import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface Document {
  id: number;
  name: string;
  is_active: boolean;
}

const documents = [{
  id: 1,
  name: 'Cédula',
  is_active: true
}, {
  id: 2,
  name: 'Comprobante de pago',
  is_active: true
}, {
  id: 3,
  name: 'Ficha del seguro social',
  is_active: true
}, {
  id: 4,
  name: 'Recibo de servicio',
  is_active: true
}];

function Documents(props: Props) {
  const { loading } = useFetch<Document[]>(
    "/",
    { method: "GET" },
    []
  );

  if (loading || !documents) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} documents={documents} />;
};

export default Documents;
