import React from 'react';
import Grid from '@material-ui/core/Grid';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import PublishIcon from '@material-ui/icons/Publish';
import CameraAltOutlinedIcon from '@material-ui/icons/CameraAltOutlined';

import { CardRadioButtons } from '../../../../../components/RadioButtons';
import STRINGS from '../../../../../strings';
import { Props as ApplicationProps } from '../index';
import { Document } from './index';

interface Props extends ApplicationProps {
  documents: Document[]
}

function Form(props: Props) {
  const theme = useTheme();
  const desktop = useMediaQuery(theme.breakpoints.up('sm'));
  const { documents } = props;
  
  function onOptionPressed() {

  }

  const options = documents.map((document) => ({
    label: document.name,
    value: document.id,
    Icon: desktop ? PublishIcon : CameraAltOutlinedIcon
  }));

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <CardRadioButtons 
          label={STRINGS.APPLICATION.PERSONAL_LOAN.DOCUMENTS.UPLOAD}
          onClick={onOptionPressed}
          options={options}
        />
      </Grid>
    </Grid>
  );
}

export default Form;