async function getToken(): Promise<string> {
  try {
    const username = process.env.REACT_APP_API_USER;
    const password = process.env.REACT_APP_API_PASS;
    
    if (!username || !password) {
      throw new Error('API username and password are not defined');
    }

    const body = {
      username,
      password
    };
  
    console.log(body, `${API_HOST}/api-token-auth`);

    const response: Response = await fetch(`${API_HOST}/api-token-auth`, {
      method: 'POST',
      mode: 'cors',
      body: new URLSearchParams(body)
    });
    
    const json = await response.json();
  
    return json.token;
  } catch (err) {
    throw err;
  }
}

function ApiProvider(props: Props) {
  const token = localStorage.getItem('jwt');

  const options: IncomingOptions = {
    mode: 'cors',
    interceptors: {
      request: async ({options}) => {
        const isValid = await verifyToken(token);

        if (!isValid) {
          const newToken = await getToken();

          options.headers = {
            ...options.headers,
            'Authorization': `JWT ${newToken}`
          };

          localStorage.setItem('jwt', newToken);

          return options;
        }

        options.headers = {
          ...options.headers,
          'Authorization': `JWT ${token}`
        };

        return options;
      }
    }
  };

  console.log(options);
  console.log(`${API_HOST}:${PORT}/api`)
  
  return (
    <FetchProvider url={`${API_HOST}:${PORT}/api`} options={options}>
      {props.children}
    </FetchProvider>
  );
}

export default ApiProvider;